.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.Maincontent {
  display: flex;
  align-items: center;
  justify-content: center;
}
.container {
  display:flex ;
  flex-direction: column;
  width: 100%;
  height: 100%;
  padding: 30px 50px 20px 50px;
  box-sizing: border-box;
  background-color: whitesmoke;
}
nav {
 
  justify-content: space-around;
  width: 100%;
  color:black;

}
.list{
  flex-direction: row;
   margin: 0;
   padding: 0;
   list-style-type: none;
}
li{
  font-size: 14px;
  margin-left: 15px;
  padding: 10px;
  box-sizing: border-box;
  color: black;
  cursor: pointer;
  display: inline-block;
}
.logo{
  font-size: 30px;
  text-transform: uppercase;

}
.main_content{
  position: relative;
  width: 70%;
  height: 100%;
  margin: 50px auto;
  padding: 50px 0 0;
  box-sizing: border-box;
  justify-content: space-between;
  flex-wrap: wrap;
}
.search {
  color: black;
  margin-left: 15px;
  
}
h1{
  position: absolute;
  left: 0;
  top: 0;
  color: black;
  font-size: 10.4vw;

}
.card{
  width: 20%;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  position: relative;
  transform: scale(.8);
  
}
.card_header{
  flex-direction: column;
  color: black;
  margin: 25px 0;
}
.price{
  font-size: 25px;
  font: bold;
}


.btn{
  width: 130px;
  height: 35px;
  font-size: 14px;
  border-radius: 35px;
}
.footer{
  width: 100%;
  justify-content: space-between;
  margin-top: auto;
  color: black;
  font-size: 12px;
}